main.video-grid {
  margin-left: var(--video-grid-margin-left);
  padding: var(--video-grid-padding);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(var(--video-grid-min-col-width), 1fr));
  gap: var(--video-grid-gap);
  box-sizing: border-box;
}

.video-card {
  background-color: var(--video-card-bg);
  border-radius: var(--video-card-radius);
  padding: var(--video-card-padding);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.video-card:hover {
  transform: translateY(var(--video-card-hover-shift));
}

.video-card video {
  width: 100%;
  border-radius: var(--video-card-radius);
}

.video-card h3 {
  font-size: var(--video-title-size);
  margin: var(--video-title-margin);
}

.video-card p {
  font-size: var(--video-desc-size);
  color: var(--video-desc-color);
  margin: var(--video-desc-margin);
}
